import React, { Component } from "react";
import axios from "axios";
import { BounceLoader } from "react-spinners";

class RecipeEdit extends Component {
  state = {
    title: "",
    image: "",
    portions: "",
    howToCookIt: "",
    imageIsLoading: false,
  };

  handleImageUpload = (event) => {
    //console.log(event.target.files[0]);

    this.setState({ imageIsLoading: true });
    const uploadData = new FormData();
    uploadData.append("image", event.target.files[0]);

    //console.log(uploadData);
    axios
      .post(
        `${process.env.REACT_APP_SERVER_URL}/upload`,
        { uploadData },
        { withCredentials: true }
      )
      .then((result) =>
        this.setState({ image: result.data.imagePath, imageIsLoading: false })
      )
      .catch((err) => this.props.history.push("/500"));
  };

  handleChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { title, image, portions, howToCookIt } = this.state;
    axios
      .patch(
        `${process.env.REACT_APP_SERVER_URL}/recipes/${this.props.match.params.id}`,
        { title, image, portions, howToCookIt },
        { withCredentials: true }
      )
      .then((result) => this.props.history.push(`/recipes/${result.data._id}`));

    // .catch((err)=>this.props.history.push("/500"))
  };

  componentDidMount() {
    axios
      .get(
        `${process.env.REACT_APP_SERVER_URL}/recipes/${this.props.match.params.id}`,
        { withCredentials: true }
      )
      .then((response) => {
        console.log(response.data.image);
        this.setState({
          title: response.data.title,
          image: response.data.image,

          portions: response.data.portions,
          howToCookIt: response.data.howToCookIt,
        });
      })
      .catch((err) => console.log(err));
  }

  render() {
    const { title, image, portions, ingredients, howToCookIt, imageIsLoading } =
      this.state;
    return (
      <div
        className="d-sm-flex flex-direction-column align-items-start text-align-start justify-content-center mt-5"
        style={{
          height: "900px",
          backgroundColor: "#D9D5DB",
        }}
      >
        <form onSubmit={this.handleSubmit}>
          <div>
            <div className="form-group mt-5">
              <BounceLoader
                color={"#D7BDE2"}
                loading={imageIsLoading}
                size={90}
              />
              <input
                onChange={this.handleImageUpload}
                type="file"
                name="image"
              />
            </div>

            <br />
            <div className="form-group m-5">
              <label htmlFor="title" style={{ width: "100px" }}>
                Title
              </label>
              <input
                name="title"
                type="text"
                value={title}
                onChange={this.handleChange}
              />
              <br />
              <div className="form-group m-5">
                <label htmlFor="portions" style={{ width: "100px" }}>
                  Portions
                </label>
                <input
                  name="portions"
                  type="text"
                  value={portions}
                  onChange={this.handleChange}
                />
              </div>
              <br />
              <div className="form-group m-5">
                {/* <Ingredients /> */}
                <label htmlFor="ingredients" style={{ width: "100px" }}>
                  Ingredients
                </label>
                <input
                  name="ingredients"
                  type="text"
                  value={ingredients}
                  onChange={this.handleChange}
                />
                <br />
              </div>

              <div className="form-group m-5 w-100 m-auto">
                <label htmlFor="howToCookIt">How To Cook It</label>
                <textarea
                  name="howToCookIt"
                  className="form-control "
                  id="exampleFormControlTextarea1"
                  rows="7"
                  value={howToCookIt}
                  onChange={this.handleChange}
                />
                <br />
              </div>
              <div className="form-group">
                <label htmlFor="typeOfDiet">Type of Diet </label>

                <select
                  className="form-group m-5"
                  id="exampleFormControlSelect1"
                  name="typeOfDiet"
                  onChange={this.handleChange}
                >
                  <option value="vegetarian">Vegetarian</option>
                  <option value="keto">Keto</option>
                  <option value="glutenFree">Gluten Free</option>
                  <option value="lowCalories">Low Calories</option>
                  <option value="rawFood">Raw Food</option>
                </select>
              </div>
              <br />
              <button
                type="submit"
                // disabled={imageIsLoading}
                style={{
                  padding: "10px",
                  margin: "auto",

                  width: "100px",
                  backgroundColor: "#4E6380",
                  color: "white",
                  fontWeight: "600",
                }}
              >
                Save
              </button>
            </div>
          </div>
        </form>
      </div>
    );
  }
}
export default RecipeEdit;
