import React, { Component } from "react";
import axios from "axios";
import { BounceLoader } from "react-spinners";

import io from "socket.io-client";
let socket = "";

class Chat extends Component {
  state = {
    allMessages: [],
    isLoading: true,
    currentMessage: "",
  };

  componentDidMount() {
    axios
      .get(
        `${process.env.REACT_APP_SERVER_URL}/chat/messages/${this.props.match.params.chatId}`
      )
      .then((response) =>
        this.setState({ allMessages: response.data, isLoading: false })
      )
      .catch((err) => console.log(err));

    console.log(process.env.REACT_APP_SOCKET_SERVER_URL);
    socket = io(`${process.env.REACT_APP_SOCKET_SERVER_URL}`);

    socket.emit("join_chat", this.props.match.params.chatId);

    socket.on("receive_message", (newMessage) => {
      this.setState({ allMessages: [...this.state.allMessages, newMessage] });
    });
  }

  handleChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  sendMessage = () => {
    let newMessage = {
      chatId: this.props.match.params.chatId,
      input: this.state.currentMessage,
      sender: this.props.user,
    };

    socket.emit("send_message", newMessage);

    this.setState({ currentMessage: "" });
  };

  render() {
    const { allMessages, isLoading, currentMessage } = this.state;

    return (
      <div>
        <h3>Welcome to Chat Room </h3>

        <div className="chatContainer">
          <div className="messages">
            {isLoading && <BounceLoader color={"#D7BDE2"} size={90} />}
            {!isLoading &&
              allMessages.map((eachMessage) => {
                return (
                  <div key={eachMessage._id} className="messageContainer">
                    <p>
                      {eachMessage.sender.username}: {eachMessage.input}
                    </p>
                  </div>
                );
              })}
          </div>

          <div className="messageInputs">
            <input
              type="text"
              placeholder="Message..."
              name="currentMessage"
              value={currentMessage}
              onChange={this.handleChange}
            />
            <button
              onClick={this.sendMessage}
              style={{
                padding: "10px",
                margin: "10px",
                width: "100px",
                backgroundColor: "#4E6380",
                color: "white",
                fontWeight: "600",
              }}
            >
              Send
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Chat;
