import axios from "axios";
import React, { Component } from "react";
// import recipeService from "../services/recipe-service";
import { Link } from "react-router-dom";
import { BounceLoader } from "react-spinners";
import SearchBar from "./SearchBar";

class RecipesList extends Component {
  state = {
    recipesList: null,
    filteredList: null,
    isLoading: true,
  };
  componentDidMount() {
    axios
      .get(`${process.env.REACT_APP_SERVER_URL}/recipes/list`, {
        withCredentials: true,
      })
      .then((response) => {
        this.setState({
          recipesList: response.data,
          filteredList: response.data,
          isLoading: false,
        });
      })
      .catch((err) => console.log(err));
    //recipeService.list.then((response) => console.log(response));//why is not working??
  }

  handleFilter = (searchQuery) => {
    console.log("Query done", searchQuery);
    const filteredList = this.state.recipesList.filter((eachRecipe) => {
      return (
        eachRecipe.name.includes(searchQuery),
        eachRecipe.typeOfDiet.includes(searchQuery),
        eachRecipe.createdBy.includes(searchQuery)
      );
    });

    this.setState({ filteredList: filteredList });
  };
  render() {
    return (
      <div>
        <SearchBar handleFilter={this.handleFilter} />

        {this.state.isLoading && <BounceLoader color={"#D7BDE2"} size={90} />}
        <div className="card-deck d-sm-flex flex-row flex-wrap justify-content-center">
          {!this.state.isLoading &&
            this.state.filteredList.map((eachRecipe) => {
              return (
                <div
                  key={eachRecipe.id}
                  className="card m-5 d-sm-flex flex-wrap-row"
                >
                  <div style={{ fontSize: "1.3rem" }}>
                    <img src="http://www.missmigas.com/wp-content/uploads/2014/10/IMG_34141.jpg" />
                    <div>
                      <Link exact to={`/recipes/${eachRecipe._id}`}>
                        {eachRecipe.title}
                      </Link>
                      <br />
                      Created by:
                      {eachRecipe.createdBy && (
                        <Link exact to={"/profile/list"}>
                          {eachRecipe.createdBy.username}
                        </Link>
                      )}
                      <br />
                      Type of Diet: {eachRecipe.typeOfDiet}
                    </div>
                  </div>
                </div>
              );
            })}
        </div>
      </div>
    );
  }
}
export default RecipesList;
