import React, { Component } from "react";
import axios from "axios";
// import { FontAwesomeIcon } from "react-icons/fa";

class Ingredients extends Component {
  state = {
    ingredients: "null",
    ingredientToAdd: "null",
  };

  handleChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };
  addIngredientFromAPI = (event) => {
    event.preventDefault();
    console.log(this.state.ingredientToAdd);
    var options = {
      method: "GET",
      url: "https://edamam-food-and-grocery-database.p.rapidapi.com/parser",
      params: { ingr: this.state.ingredientToAdd },
      headers: {
        "x-rapidapi-host": "edamam-food-and-grocery-database.p.rapidapi.com",
        "x-rapidapi-key": process.env.REACT_APP_FOOD_API,
      },
    };

    axios
      .request(options)
      .then((response) => {
        console.log(response.data.parsed[0].food);
        this.setState({
          ingredients: response.data.parsed[0].food,
          ingredientToAdd: response.data.parsed[0].food,
        });
        // ingredients:[... this.state.ingredients, newIngredient]
        console.log("form send");
        // this.e.target.reset();
        // add the food to the ingredients state
        // clear the input field for add ingredient
        this.props.history.push("/recipes/ingredients");
      })
      .catch((error) => {
        console.error(error);
      });
  };

  render() {
    const { ingredientToAdd } = this.state;
    return (
      <div className="form-group mt-5">
        <form onSubmit={this.addIngredientFromAPI}>
          <label htmlFor="ingredients" style={{ width: "100px" }}>
            Ingredients
          </label>
          <input
            name="ingredientToAdd"
            placeholder="add ingredient"
            onChange={this.handleChange}
            value={ingredientToAdd}
          />

          <br />
          <button
            type="submit"
            className="btn btn-secondary:hover mt-4"
            style={{
              marginLeft: "1.3rem",
              padding: "5px",
              width: "150px",
              backgroundColor: "#4E6380",
              color: "white",
              fontWeight: "600",
            }}
          >
            Add
          </button>
        </form>
      </div>
    );
  }
}
export default Ingredients;
